.photo {
    display: block;

    height: 150px;
    min-width: 150px;
    
    object-fit: cover;
    object-position: center;
}

.dots {
    position: initial !important;
}

.photosBlock {
    display: flex;
    flex-direction: column;
    align-items: center;

    gap: 20px;
}